#root, html, body {
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
    height: 100vh;
}

.app {
    height: 100%;
    background: linear-gradient(rgba(0,0,0,0.3), rgba(0, 0, 0, 0.5)), url(./img/zbg.jpg);
    background-size: cover;
    background-position: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: 'Poppins', sans-serif;

}

.card {
    width: 45%;
    height: 30%;
    padding: 25px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 0px;
    background-color: #efefef;
    box-shadow: 5px 5px 10px ;
}

.heading {
    height: 580px;
    display: flex;
    align-items: center;
    font-weight: 300;
    font-size: 20px;
}
.btn {
    margin-top: 30px;
    padding: 15px 20px;
    font-size: 15px;
    border: none;
    cursor: pointer;
    box-shadow: inset 0px 0px 0px 0px crimson, 5px 5px 10px #000;
    transition: 0.5s ease;
}
.btn:hover {
    box-shadow: inset 0px 50px 0px 0px crimson, 5px 5px 10px #000;
    color: #fff;
}

@media(max-width: 700px){
    .btn {
        background-color: crimson;
        color: white;
    }
}